@import 'src/style/vars.scss';

.header {
    background-color: $primary;
    height: $header-height;
    width: 100%;
    display: flex;
    justify-content: center;
    color: #ffffff;
    box-shadow: 0px 0px 6px $primary;
    .container {
        align-items: center;
        justify-content: space-between;
        @media screen and (max-width: 1200px) {
            padding: 0px 8px;
        }
        .logo {
            text-decoration: none;
            h2 {
                color: #ffffff;
            }
        }
        .navbar {
            display: flex;
            align-items: center;
            .profile-btn {
                border: none;
                background: linear-gradient(90deg, rgb(235, 194, 47) 0%, rgb(241, 192, 15) 100%);
                padding: 8px;
                border-radius: 5px;
                cursor: pointer;
                height: 32px;
                margin-left: 32px;
                display: flex;
                align-items: center;
                justify-content: space-around;
                font-size: 16px;
                img {
                    width: 16px;
                    height: 16px;
                    margin-right: 4px;
                }
            }
            a {
                text-decoration: none;
                color: $font-dark;
            }
        }
    }
}